.lpf-input {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 5% 12%;
    grid-gap: 2%;
}

.lpf-input>div>input,
.file-input>label {
    border-radius: 50px;
    margin: 5% 0;
}

.file-input>label {
    margin: 5% 2%;
}

.file-input>label {
    background: rgb(6, 150, 225);
}

.lpf-input>div>h4 {
    font-weight: bold;
}

.lpf-submit>input {
    width: 30%;
    border-radius: 50px;
    margin: 2% auto;
}

.lpf-submit>button {
    padding: 1% 5%;
    background: rgb(6, 150, 225);
}

.carousel-indicators>li {
    background: #F5F5F5;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: 2px solid #111;
    margin: 0 1rem;
}

.carousel-indicators>.active {
    background: #111;
}

.carousel-height {
    height: 50vh;
    min-height: 500px;
}

.carousel-height>h2 {
    padding-top: 10%;
}

hr {
    margin: 1rem 0;
    width: 95%;
}

.mb-0>button {
    font-weight: bold;
}

.about-img {
    background-size: cover;
    background-position: center;
    background-image: url("img/lpf/car.jpg");
    min-height: 500px;
}

/* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */

@media (max-width: 768px) {
    .blank {
        display: none;
    }
    .back-left,
    .back-right {
        padding: 10%;
    }
    .container-info {
        grid-template-columns: repeat(2, 1fr);
        margin: 10%;
    }
    .container-about-us {
        grid-template-columns: 1fr;
        margin: 0 20%;
    }
    .about-us-item {
        margin: 10% 0;
    }
    .products-grid {
        grid-template-columns: repeat(2, 1fr);
    }
    .footer-item {
        margin: 10% 0;
    }
    footer,
    .container-cookies,
    .product-input-grid,
    .effect-grid,
    .identity,
    .section-card-grid,
    .leaflets-grid,
    .business-card-product-grid,
    .leaflets-sizes-grid,
    .container-news,
    .container-back,
    .container-spotuv,
    .news-section-item,
    .lpf-input,
    .lpf-grid,
    .fancy-papers-grid {
        grid-template-columns: 1fr;
    }
    .icon-container {
        grid-template-columns: repeat(6, 1fr);
    }
    .news-item {
        -webkit-transform: translateX(10%);
            -ms-transform: translateX(10%);
                transform: translateX(10%);
        margin-right: 15%;
    }
}

@media (min-width: 768px) and (max-width: 1024px) {
    .news-section-item,
    .product-input-grid,
    .identity,
    .section-card-grid,
    .container-news {
        grid-template-columns: 1fr;
    }
    .products-grid {
        grid-template-columns: repeat(3, 1fr);
    }
    .leaflets-sizes-grid,
    .effect-grid,
    .leaflets-grid,
    .business-card-product-grid,
    .lpf-grid,
    .fancy-papers-grid {
        grid-template-columns: repeat(2, 1fr);
    }
    .news-item {
        -webkit-transform: translateX(10%);
            -ms-transform: translateX(10%);
                transform: translateX(10%);
        margin-right: 15%;
    }
}

@media (min-width: 1024px) and (max-width: 1224px) {}

.App {
    -webkit-transition: all 0.7s;
    -o-transition: all 0.7s;
    transition: all 0.7s;
    opacity: 0;
}


